import React from 'react';
import ReactDOM from 'react-dom';
import TabsPanel from './TabsPanel';
import Tab from './Tab';


// This class represents the select option tags along with doing the Maps API calling and OpeanWeather API calling.
//Binding of on click events in constructor.
//Map API is first taking ZIP codes values to provide the latitutdes 
//and longitudes which then is used in Google maps to show the locations.
class SelectComponent extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            content: [],
            selectedIndex: -1
        }
        this.onChange = this.onChange.bind(this);
        this.renderTabPanel = this.renderTabPanel.bind(this);

    }
//This function does the API calling for maps and openweather API and fetches the data 
    onChange(event) {
        let apiContent = [];
        let self = this;
        let indexValue = event.nativeEvent.target.selectedIndex;
        let index = event.nativeEvent.target[indexValue].value;
        let lat = "";
        let long = "";
        let url = "https://api.openweathermap.org/data/2.5/forecast?zip=" + index + "&units=imperial&APPID=cc416c026550bcc9427d02b9f040c896"
        fetch("https://maps.googleapis.com/maps/api/geocode/json?address=" + index + "&key=AIzaSyClTqcMNpFm9_rFaaXH6ptzDpmTmAEwml4")
            .then(maps => maps.json())
            .then(maps => {
                lat = maps.results[0].geometry.location.lat; long = maps.results[0].geometry.location.lng;
                fetch(url)
                    .then(response => response.json())
                    .then((response1) => {

                        const dailyData = response1.list.filter(reading => reading.dt_txt.includes("18:00:00"))


                        for (let i = 0; i < dailyData.length; i++) {


                            var dic = {};
                            dic.Temperature = dailyData[i].main.temp + "Â°F";
                            dic.Humidity = dailyData[i].main.humidity;
                            dic.Date = dailyData[i].dt;
                            dic.Wind = dailyData[i].wind.speed + " km/hr";
                            dic.AirPressure = dailyData[i].main.pressure + " hPa";
                            dic.Forcast = dailyData[i].weather[0].description;
                            dic.lat = lat;
                            dic.long = long;
                            apiContent.push(dic);
                        }


                        self.setState({
                            content: apiContent,
                            selectedIndex: indexValue

                        }

                        )
                    })
            })
    }

//Function for rendering the tabspanels.
    renderTabPanel() {
        var display = false;

        return (
            <TabsPanel value={this.state.content} />
        );
    }


//rendering all the select option and calling the render tab panel function .
    render() {


        return (

            <div>
                {
                    <select defaultValue="Search..." onChange={this.onChange}>
                        <option value="">Choose the Cities</option>
                        <option value="02122" >Boston</option>
                        <option value="98101">Seattle</option>
                        <option value="92602">Irvine</option>
                        <option value="14623">Rochester</option>
                        <option value="90712">LongBeach</option>
                        <option value="10090">New York City</option>
                        <option value="85004">Phoenix</option>
                    </select>
                }


                {
                    this.renderTabPanel()
                }


            </div>


        );
    }
}

export default SelectComponent;
