import React from 'react';
import ReactDOM from 'react-dom';
import Tabs from './Tab';
import DisplayPanel from './DisplayPanel';
import MapContainer from './MapContainer';
import './TabsPanel.css'

//class for Tabs Panel.This class is creating a structure for tabs and a map .
//Binding the onclick events.
class TabsPanel extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            activeTab: 0
        }

        this.renderDisplayPanel = this.renderDisplayPanel.bind(this);
        this.onTabClick = this.onTabClick.bind(this);
    }
//this function is setting the state of activetab as tab id's.
    onTabClick(tabId) {
        this.setState({ activeTab: tabId });
    }
//this function is setting the value of DisplayPanel 
    renderDisplayPanel() {

        if (this.props.value.length > 0) {
            return (
                <div>

                    <DisplayPanel value={this.props.value[this.state.activeTab]} />

                </div>

            );
        }
        else {
            return <div />
        }
    }
//rendering all the tabs panel value along with on click functionality
    render() {
        if (this.props.value.length > 0) {
            return (

                <div className="tabpanel-row">
                    <div className="tabpanel-columns">
                        <Tabs value={this.props.value[0]} onTabClick={this.onTabClick} tabId={0} />
                    </div>
                    <div className="tabpanel-columns">
                        <Tabs value={this.props.value[1]} onTabClick={this.onTabClick} tabId={1} />
                    </div>
                    <div className="tabpanel-columns">
                        <Tabs value={this.props.value[2]} onTabClick={this.onTabClick} tabId={2} />
                    </div>
                    <div className="tabpanel-columns">
                        <Tabs value={this.props.value[3]} onTabClick={this.onTabClick} tabId={3} />
                    </div>
                    <div className="tabpanel-columns">
                        <Tabs value={this.props.value[4]} onTabClick={this.onTabClick} tabId={4} />
                    </div>

                    <div className="displaypanel-div">
                        {
                            this.renderDisplayPanel()
                        }</div>
                    <div className="map-div">

                        <MapContainer lat={{ ...this.props.value[0] }.lat} long={{ ...this.props.value[0] }.long} />
                    </div>

                </div >

            );
        }
        else {
            return <div></div>;
        }

    }




}

export default TabsPanel;