import React from 'react';
import ReactDOM from 'react-dom';
import Img from 'react-image';
import Moment from 'react-moment';
import 'moment-timezone';
import DisplayPanel from './DisplayPanel';
import './Tab.css'
var moment = require('moment');

//This class fills up all the tabs represented as images .
//Usage of moment.js in this class for formatting the dates.
class Tabs extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            display: false,
            content: {}


        }
        this.renderTabs = this.renderTabs.bind(this);

        this.tabOnClick = this.tabOnClick.bind(this);

    }

//Function that fills tabs with images and formats the date
    renderTabs() {
        let newDate = new Date();
        newDate.setTime({ ...this.props.value }.Date * 1000)

        let imgUrl = "";
        switch ({ ...this.props.value }.Forcast) {
            case "few clouds":
            case "scattered clouds":
            case "few clouds":
            case "broken clouds":
            case "overcast clouds":
                imgUrl = 'https://media.giphy.com/media/Ke7i5t6QDmDSO82Uga/giphy.gif'

                break;

            case "light rain":
            case "moderate rain":
                imgUrl = 'https://media.giphy.com/media/3oKIPstwMF15FghbYQ/giphy.gif'
                break;
            case "clear sky":
                imgUrl = 'https://media.giphy.com/media/xM8CKTlDAN5Xa/giphy.gif'
                break;

            case "light snow":
                imgUrl ='https://media.giphy.com/media/bnH2hkYp97rGM/giphy.gif'
                 break;

            default:

                imgUrl = 'https://media.giphy.com/media/xM8CKTlDAN5Xa/giphy.gif'
                break;
        }

        return (
            <div className="tab-div">
                <div class="img-div">
                    <Img src={imgUrl} className="images" />
                </div>
                <div className="date-tab">
                    <h3 className="card-title">{moment(newDate).format('ddd')}</h3>
                    <h3 className="text-muted">{moment(newDate).format('MMM Do')}</h3>
                </div>
            </div>
        );
    }

//function for  click on tabs
    tabOnClick() {
        this.props.onTabClick(this.props.tabId)
    }


//rendering and returning the clickable tabs.
    render() {
        return (
            <div>
                <div onClick={this.tabOnClick}>
                    {
                        this.renderTabs()
                    }
                </div>

            </div>
        );
    }
}

export default Tabs;