import React from 'react';
import ReactDOM from 'react-dom';
import { Map, GoogleApiWrapper, Marker } from 'google-maps-react';

//This class shows the MAP setup . The lat and longs values are passed from props that has its values.
const Balloon = () => <img width="25" src='https://www.speedwaymiracletournament.com/' />;
class MapContainer extends React.Component {
  constructor(props) {
    super(props)
    this.renderMap = this.renderMap.bind(this)

  }

//setting the maps
  renderMap() {


    const mapStyles = {
      width: '40%',
      height: '42%',
    };
    return (
      <Map className="mapsize"
        google={this.props.google}
        zoom={6}
        style={mapStyles}
        initialCenter={{ lat: this.props.lat, lng: this.props.long }}
        center={{ lat: this.props.lat, lng: this.props.long }}
      >
        <Marker position={{ lat: this.props.lat, lng: this.props.long }} />
      </Map>
    );

  }
//rendering and returning the renderMap function to display on Div
  render() {
    return (
      <div>{
        this.renderMap()
      }
      </div>
    );
  }
}

export default GoogleApiWrapper({
  apiKey: 'AIzaSyClTqcMNpFm9_rFaaXH6ptzDpmTmAEwml4'
})(MapContainer);

