import React from 'react';
import ReactDOM from 'react-dom';
import Tabs from './TabsPanel';
import TabsPanel from './TabsPanel';
import './Display.css'


//This class displays all the weather data points.
class DisplayPanel extends React.Component {
    constructor(props) {
        super(props)
        this.renderContent = this.renderContent.bind(this);

    }

//function that displays all the  weather data points in the table.
    renderContent() {

        var content = { ...this.props.value }
        return (
            <div className="display-div">
                <table>
                    <h3>
                        <tr><td>Temperature:</td> <td>{content.Temperature}</td></tr>
                    </h3>
                    <h3>
                        <tr><td>Humidity:</td> <td>{content.Humidity}</td></tr>
                    </h3>
                    <h3>
                        <tr><td>Wind:</td> <td>{content.Wind}</td></tr>
                    </h3>
                    <h3>
                        <tr><td>AirPressure:</td> <td>{content.AirPressure}</td></tr>
                    </h3>
                    <h3>
                        <tr><td>Forecast:</td> <td>{content.Forcast}</td></tr>
                    </h3>
                </table>
            </div>
        );
    }

//rendering the renderContent function to display the data points.
    render() {
        return (
            <div>
                {
                    this.renderContent()
                }
            </div>
        );
    }
}
export default DisplayPanel;